    <section class="privacy-content">
      <!--      <span class="privacy-content-title">隐私政策条款</span>-->
      <b>隐私保护指引版本更新提示：      </b>
      <span>感谢您使用脉迩生活 。我们对《脉迩生活隐私保护指引》进行了更新，更新时间12月8日，生效时间12月8日      </span>
      <b>主要包括以下内容：      </b>
      <span>1、调整并优化了我们收集使用的信息，服务优化及安全与注册中的信息采集说明，第三方推送所使用的sdk标注。推送均调用第三方极光推送接口完成app推送功能</span>
      <span>2、我们通过采用第三方平台友盟提供的SDK，获取到您的陀螺仪传感器权限，以达到精准定位即时推送的功能</span>
      <br>
      <span>本政策适用于由上海移视网络科技有限公司（以下简称“移视科技”或“我们”）提供的“脉迩生活”之产品和服务，包括您通过“脉迩生活”产品的应用程序（APP）、公众号、小程序、应用程序编程接口（API）、软件开发工具包接口（SDK）等方式访问或使用的我们的产品和/或服务。</span>
      <br>
      <b>本政策将帮助您了解以下内容：</b>
      <span>(1) 我们收集和使用您的个人信息；</span>
      <span>(2) 我们如何存储您的个人信息；</span>
      <span>(3) 我们如何共享、转让、公开披露您的个人信息；</span>
      <span>(4) 我们如何使用您的个人信息；</span>
      <span>(5) 我们如何保护您的个人信息；</span>
      <span>(6) 您的权利；</span>
      <span>(7) 未成年人的个人信息保护；</span>
      <span>(8) 第三方SDK使用说明；</span>
      <span>(9) 本政策如何更新；</span>
      <span>(10) 如何联系我们。</span>
      <br>
      <span>我们深知个人信息对您的重要性，并会尽全力保护您的个人信息安全、可靠。同时，我们承诺，我们将按业界成熟的安全标准，采取相应的安全保护措施来保护您的个人信息。请在使用我们的产品和/或服务前，仔细阅读并了解本政策。本政策中，使用加粗字体显著标识的内容，表示这些属于您的个人敏感信息，表示这些内容需要您特别关注或重点注意。</span>
      <br>
      <b>1. 我们如何收集和使用您的个人信息</b>
      <span>在您使用脉迩生活产品和/或服务的过程中，我们会按照如下方式收集您在使用服务时主动提供或因为使用服务而产生的信息，用以向您提供服务、优化我们的服务以及保障您的帐号安全：</span>
      <span>1.1	当您注册脉迩生活生活时，我们会经您同意后获取您的微信登录授权，并收集您的手机号码；您可以直接选择通过手机号注册而不提供微信登录授权。收集这些信息是为了帮助您完成注册，保护您的账号安全。手机号码属于敏感信息，收集此类信息是为了满足相关法律法规的网络实名制要求。若您不提供这类信息，您可能无法正常使用我们的服务。您还可以根据自身需求选择填写头像、昵称等信息，以方便您在使用我们的过程中，获得更好的体验。</span>
      <span>1.2	当您使用脉迩生活生活产品和/或服务时，为保障您正常使用我们的服务，维护我们服务的正常运行，改进及优化我们的服务体验以及保障您的帐号安全，我们会收集您的设备型号、操作系统、唯一设备标识符、我们软件版本号、设备加速器（如重力感应设备）、操作日志、服务日志信息（您在脉迩生活搜索、查看的信息、服务故障信息）等日志信息，这类信息是为提供服务必须收集的基础信息。</span>
      <span>1.3	当您使用我们的私人医生健康计划时，我们会根据医生要求收集您的 身高、体重、年龄、性别信息，收集此类信息是为了给您定制更加适合的健康计划。上述信息属于敏感信息，这些信息是需要您主动填写的，您也可以选择不填写，不会影响您正常使用我们APP的功能。</span>
      <span>1.4	我们为用户提供健康评估服务，在使用该服务时，我们会通过问卷/问答的方式收集您对问卷问题的回答，以实现健康评估报告的呈现。若您不提供这类信息，您可能无法正常使用上述功能，但不会影响您正常使用其他功能。</span>
      <span>1.5	我们为用户提供健康档案储存、查询服务，为实现此服务，我们会收集您的个人信息资料（姓名、性别、生日、证件类型、证件号码、身高、体重、婚姻状况、职业、医保类型）、健康史（家族史、疾病史、过敏史、用药史、手术史、吸烟史、饮酒史）、健康数据（运动步数、睡眠时长、体重、血糖、血压）、健康评估结果、体检报告信息。</span>
      <span>1.6	当您使用我们的私人医生在线咨询功能时，您的病情描述、检查报告、问诊记录信息会存储在我们的服务器中，因为存储是实现这一功能所必须的，方便您今后查询自己的问诊过程和问诊记录。除非经您自主选择或遵从相关法律法规要求，我们不会对外提供上述信息。</span>
      <span>1.7	我们的医生在线咨询服务（包括视频医生、在线医生、医生助手团队）由外部第三方提供，当您使用此类功能时，我们会在经由您的同意后，将您的手机号提供给外部第三方，这是实现此类服务流程所必须的，拒绝提供该信息仅会使您无法使用上述功能，但不影响您正常使用我们其他功能。</span>
      <span>1.8	当您使用我们的扫一扫功能时，我们需要使用您手机的相机权限，我们会在您使用时询问，经由您同意才会使用。如果您想拒绝我们使用，可以打开手机“设置”——点击“应用权限管理”——找到脉迩生活APP——解除权限</span>
      <span>1.9	当您使用我们的紧急联系人功能时，我们需要使用您手机的位置权限和通讯录权限，我们会在您使用时询问，经由您同意才会使用。如果您想拒绝我们使用，可以打开手机“设置”——点击“应用权限管理”——找到脉迩生活APP——解除权限</span>
      <span>1.10	另外，根据相关法律法规及国家标准，以下情形中，我们可能会收集、使用您的相关个人信息无需征求您的授权同意：</span>
      <span>(1)	与国家安全、国防安全等国家利益直接相关的；与公共安全、公共卫生、公众知情等重大公共利益直接相关的；</span>
      <span>(2)	与犯罪侦查、起诉、审判和判决执行等直接相关的；</span>
      <span>(3)	出于维护您或其他个人的生命、财产、声誉等重大合法权益但又很难得到本人同意的；</span>
      <span>(4)	所收集的个人信息是您自行向社会公众公开的；</span>
      <span>(5)	从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道；</span>
      <span>(6)	根据您要求签订和履行合同所必需的；</span>
      <span>(7)	用于维护所提供的产品或服务的安全稳定运行所必需的，例如发现、处置产品或服务的故障；</span>
      <span>(8)	为开展合法的新闻报道所必需的；</span>
      <span>(9)	出于公共利益开展统计或学术研究所必要，且其对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的；</span>
      <span>(10)	法律法规规定的其他情形。</span>
      <span>请您理解，我们向您提供的功能和服务是不断更新和发展的，如果某一功能或服务未在前述说明中且收集了您的信息，我们会通过页面提示、交互流程、网站公告等方式另行向您说明信息收集的内容、范围和目的，以征得您的同意。</span>
      <span>如您的信息（如搜索关键字信息）无法单独或结合其他信息识别到您的个人身份，其不属于法律意义上您的个人信息；只有当您的信息可以单独或结合其他信息识别到您的个人身份时，则在结合使用期间，将作为您的个人信息按照本政策处理与保护。</span>
      <br>
      <b>2. 我们如何存储您的个人信息</b>
      <span>2.1	信息存储的地点：我们会按照法律法规规定，将境内收集的用户个人信息存储于中国境内。</span>
      <span>2.2	信息存储的期限：一般而言，我们仅为实现目的所必需的时间保留您的个人信息，例如：手机号码，若您需要使用脉迩生活服务，我们需要一直保存您的手机号码，以保证您正常使用该服务，当您注销脉迩生活帐户后，我们将删除相应的信息；当我们的产品或服务发生停止运营的情形时，我们将以推送通知、公告等形式通知您，并在合理的期限内删除您的个人信息或进行匿名化处理。</span>
      <br>
      <b>3. 我们如何共享、转让、公开披露您的个人信息</b>
      <span>3.1	我们不会与第三方公司、组织和个人共享您的个人信息，但以下情况除外：</span>
      <span>(1)	您自行提出要求的；</span>
      <span>(2)	征得您明确同意的；</span>
      <span>(3)	根据法律、行政法规、部门规章等规定，或政府部门提出的强制性要求；</span>
      <span>(4)	脉迩生活集团内部的必要共享：我们只会共享必要的个人信息，且受本政策中所声明目的的约束。如要改变个人信息的处理目的，将再次征求您的授权同意。</span>
      <span>(5)	与授权合作伙伴共享：仅为实现本政策中声明的目的，我们可能会与合作伙伴共享您的某些个人信息，以提供更好的客户服务和用户体验。我们仅会出于合法、正当、必要、特定、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息。我们的合作伙伴无权将共享的个人信息用于任何其他用途。我们的合作伙伴包括：</span>
      <span>a)	提供技术服务的合作伙伴：我们会与提供该服务的合作伙伴共享您的相关信息，以统计、分析用户在使用我们产品过程中出现的BUG、崩溃、卡顿等情况，以维护/改进我们的产品和/或服务。提供支付服务的合作伙伴：为实现您支付的需求，我们会与支付合作伙伴共享必要的信息。</span>
      <span>b)	提供第三方登录服务的合作伙伴：为实现您使用第三方账号登录的功能，我们会与提供该服务的合作伙伴共享您的必要信息。</span>
      <span>我们与上述合作伙伴共享的均是无法直接识别到您个人身份的信息，且对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的保密协议，要求他们按照我们的说明、本政策以及其他任何相关的保密和安全措施来处理个人信息。如果我们共享能够直接识别到您身份的个人信息（如手机号码、身份证号等），会再次征得您的同意。</span>
      <span>3.2	除以下情况外，我们不会向第三方公司、组织和个人转让您的个人信息：</span>
      <span>(1)	征得您明确同意的；</span>
      <span>(2)	在涉及合并、收购或破产清算时，如涉及到个人信息转让，我们会要求新的持有您个人信息的公司、组织继续受本政策的约束，否则我们将要求该公司、组织重新向您征求授权同意。</span>
      <span>3.3	我们仅会在以下情况下，且采取符合业界标准的安全防护措施的前提下，才会公开披露您的个人信息：</span>
      <span>(1)	披露方式和披露的个人信息范围征得您明确同意的；</span>
      <span>(2)	基于法律规定的披露：在法律、法规规定、诉讼或政府主管部门强制性要求的情况下，我们可能会公开披露您的个人信息，但我们会核实上述机构是否具有相应的法律、法规或其他法律文件依据，如传票或调查函。</span>
      <span>3.4	征得同意的例外</span>
      <span>请您充分知晓，以下情形中，共享、转让、公开披露您的个人信息的，无需事先征得您的授权同意：</span>
      <span>(1)	与国家安全、国防安全直接相关的；</span>
      <span>(2)	与公共安全、公共卫生、重大公共利益直接相关的；</span>
      <span>(3)	与犯罪侦查、起诉、审判和判决执行等直接相关的；</span>
      <span>(4)	出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</span>
      <span>(5)	您自行向社会公众公开的个人信息；</span>
      <span>(6)	从合法公开披露的信息中收集的您的个人信息，如合法的新闻报道、政府信息公开等渠道。</span>
      <br>
      <b>4. 我们如何使用您的个人信息</b>
      <!--      <span>4.1	我们可能将通过某些功能所收集的信息用于我们的其他服务。例如，我们可能将您在使用我们某一功能或服务时我们收集的信息，在另一功能或服务中用于向您提供特定内容，包括但不限于展示广告对您阅读过的内容进行信息安全类提示、基于特征标签进行间接人群画像并提供更加精准和个性化的服务和内容等。</span>-->
      <span>4.1	为了确保服务的安全，帮助我们更好地了解我们应用程序的运行情况，我们可能记录相关信息，例如，您使用应用程序的频率、崩溃数据、总体使用情况、性能数据以及应用程序的来源。我们不会将我们存储在分析软件中的信息与您在应用程序中提供的任何个人身份信息相结合。</span>
      <br>
      <b>5. 我们如何保护您的个人信息</b>
      <span>5.1	我们努力为用户的信息安全提供保障，以防止信息的丢失、不当使用、未经授权访问或披露：</span>
      <span>(1)	我们将在合理的安全水平内使用各种安全保护措施以保障信息的安全。例如，我们会使用加密技术（例如，SSL）、匿名化处理等手段来保护您的个人信息。</span>
      <span>(2)	我们通过不断提升的技术手段加强安装在您设备端的软件的安全能力，以防止您的个人信息泄露。例如，我们为了安全传输会在您设备本地完成部分信息加密的工作；为了预防病毒、木马程序或其他恶意程序、网站会了解您设备安装的应用信息或正在运行的进程信息。</span>
      <span>(3)	我们建立专门的管理制度、流程和组织以保障信息的安全。例如，我们严格限制访问信息的人员范围，要求他们遵守保密义务，并进行审计。</span>
      <span>(4)	若发生个人信息泄露等安全事件，我们会启动应急预案，阻止安全事件扩大，并以推送通知、公告等形式告知您。</span>
      <span>5.2	尽管已经采取了上述合理有效措施，并已经遵守了相关法律规定要求的标准，但请您理解，由于互联网环境并非百分之百安全，且因为技术的限制以及可能存在的各种恶意手段，即便竭尽所能加强安全措施，也不可能始终保证信息百分之百的安全，我们将尽力确保您提供给我们的个人信息的安全性。您知悉并理解，您接入我们的服务所用的系统和通讯网络，有可能因我们可控范围外的因素而出现问题。因此， 我们建议您采取积极措施保护个人信息的安全，包括但不限于使用复杂密码、定期修改密码、不将自己的账号密码等个人信息透露给他人。</span>
      <br>
      <b>6. 您的权利</b>
      <span>按照中国相关的法律、法规、标准，以及其他国家、地区的通行做法，我们保障您对自己的个人信息行使以下权利：</span>
      <span>6.1	访问您的个人信息</span>
      <span>您有权访问您的个人信息，法律法规规定的例外情况除外。如果您想行使数据访问权，可以通过以下方式自行访问：进入脉迩生活APP后，点击我的——点击头像栏——可以查看头像、账号、昵称、手机。</span>
      <span>6.2	更正您的个人实名认证信息</span>
      <span>当您发现我们处理的关于您的个人实名认证信息有错误时，您有权要求我们作出更正。您可以通过拨打400-0211-100客服电话的方式提出更正申请。</span>
      <span>如果您无法通过上述链接更正这些个人信息，您可以发送电子邮件至customers@mvtech.cc与我们取得联系，我们将在15日内回复您的更正请求</span>
      <span>6.3	删除您的个人信息</span>
      <span>在以下情形中，您可以向我们提出删除个人信息的请求：</span>
      <span>(1)	如果我们处理个人信息的行为违反法律法规；</span>
      <span>(2)	除本政策特别约定外，我们收集、使用您的个人信息，却未征得您的同意；</span>
      <span>(3)	如果我们处理个人信息的行为违反了与您的约定；</span>
      <span>(4)	如果您不再使用我们的产品或服务，或您注销了账号；</span>
      <span>(5)	如果我们不再为您提供产品或服务。</span>
      <span>若我们决定响应您的删除请求，我们还将同时通知从我们获得您的个人信息的实体，要求其及时删除，除非法律法规另有规定，或这些实体获得您的独立授权。</span>
      <span>当您从我们的服务中删除信息后，我们可能不会立即在备份系统中删除相应的信息，但会在备份更新时删除这些信息。</span>
      <span>6.4	撤回您的授权或改变您授权同意的范围</span>
      <span>(1)	请您理解，每个业务功能需要一些必要的个人信息才能得以完成，当您撤回同意或授权后，我们无法继续为您提供撤回同意或授权所对应的服务和功能，也不再处理您相应的个人信息。但您撤回同意或授权的决定，不会影响此前基于您的授权而开展的个人信息处理。对于我们收集的非必要个人信息，您可以随时给予或收回您的授权同意。</span>
      <span>a)	您可以通过以下方式自行操作：</span>
      <span>进入脉迩生活APP后，点击我的——点击页面右上角的“设置”——点击“隐私设置”，然后您可以逐项关闭您的授权。</span>
      <span>(2)	如果您不想接受我们给您发送的商业广告，您随时可通过以下方式取消：</span>
      <span>a)	安卓用户：打开手机“设置”——点击“通知与状态栏”——点击“通知管理”——找到脉迩生活APP——关闭通知</span>
      <span>b)	iphone用户：打开手机“设置”——点击“通知”——找到脉迩生活APP——关闭通知</span>
      <span>(3)	对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份磁带上存放的信息）的请求，我们可能会予以拒绝。</span>
      <span>(4)	在以下情形中，我们将无法响应您的请求：</span>
