      <b>7. 未成年人的个人信息保护</b>
      <span>我们非常重视对未成年人个人信息的保护。根据相关法律法规的规定，若您是未满18周岁的未成年人，在使用我们的服务之前，请您让您的父母或监护人阅读本政策，并在提交您的个人信息之前获得您监护人的同意和指导。</span>
